import axios from 'axios'
import qs from 'qs'
// axios 配置
// axios.defaults.timeout = 5000;
// axios.defaults.baseURL = 'https://api.it919.cn/public/api'; 
// axios.defaults.withCredentials = true;  //表示跨域请求时是否需要使用凭证


// 创建axios实例
const service = axios.create({
    baseURL: 'https://api.it919.cn/public/api', // api的base_url
    //baseURL: process.env.BASE_API, // api的base_url
    timeout: 5000,                  // 请求超时时间
    withCredentials: true,   //加了这段就可以跨域了
    headers: {'X-Requested-With': 'XMLHttpRequest'},  // `headers` 是即将被发送的自定义请求头
    transformRequest: [function (data) { //  只能用在 'PUT', 'POST' 和 'PATCH' 这几个请求方法
        data = qs.stringify(data)
        return data
    }],
    data: {
        version: "v1.0",
        platform: 'pcweb'
    },
});


//request拦截器, POST传参序列化
service.interceptors.request.use((config) => {

    if (config.method === 'post') {
        config.data = qs.stringify(config.data);
    }
    config.headers['Accept'] = 'text/plain';
    config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
    return config;
}, (error) => {
    console.log(error); // for debug
    return Promise.reject(error);
});


/**
 * 
 * POST 请求方式
 * @param {string} url     请求URL
 * @param {object} params  请求参数
 * @returns 
 */


// respone拦截器
service.interceptors.response.use(
    response => response,
    /**
     * 下面的注释为通过response自定义code来标示请求状态，当code返回如下情况为权限有问题，登出并返回到登录页
     * 如通过xmlhttprequest 状态码标识 逻辑可写在下面error中
     */
    //  const res = response.data;
    //     if (res.code !== 20000) {
    //       Message({
    //         message: res.message,
    //         type: 'error',
    //         duration: 5 * 1000
    //       });
    //       // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
    //       if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
    //         MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
    //           confirmButtonText: '重新登录',
    //           cancelButtonText: '取消',
    //           type: 'warning'
    //         }).then(() => {
    //           store.dispatch('FedLogOut').then(() => {
    //             location.reload();// 为了重新实例化vue-router对象 避免bug
    //           });
    //         })
    //       }
    //       return Promise.reject(error);
    //     } else {
    //       return response.data;
    //     }
    error => {
        console.log('err' + error);// for debug
        // Message({
        //     message: error.message,
        //     type: 'error',
        //     duration: 5 * 1000
        // });
        return Promise.reject(error);
    }
)



export default service;




